@keyframes jello {
  11.1% {
    transform: none
  }

  22.2% {
    transform: skewX(-8.5deg) skewY(-1.5deg) translateX(-30px);
  }

  33.3% {
    transform: skewX(4.25deg) skewY(4.25deg) translateX(10px);
  }

  44.4% {
    transform: skewX(-15.125deg) skewY(-5.125deg) translateX(-20px);
  }

  55.5% {
    transform: skewX(5.5625deg) skewY(5.5625deg) translateX(10px);
  }

  66.6% {
    transform: skewX(-4.78125deg) skewY(-4.78125deg) translateX(-10px);
  }

  77.7% {
    transform: skewX(3.390625deg) skewY(3.390625deg) translateX(10px);
  }

  88.8% {
    transform: skewX(-2.1953125deg) skewY(-2.1953125deg) translateX(-10px);
  }

  100% {
    transform: none
  }
}

